* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

:root {
  font-size: 62.5%;
}

body {
  width: 100%;
  grid-template: "h h h" 100px
                 ". m ." 1fr
                 "s s s" 1fr
                 "f f f" 150px
                 / 10vw 1fr 10vw;
  font-family: Arial, Helvetica, sans-serif;
  display: grid;
}

header {
  width: 100%;
  height: 100px;
  background-color: #d3d3d3;
  grid-area: h;
  padding: 0 10vw;
}

