  align-items: flex-end;
  gap: 2rem;
  margin-left: 2rem;
  padding-top: .7rem;
  font-size: 1.3rem;
  display: flex;
}

main {
  grid-area: m;
  grid-template-rows: 100px 90px 25px 25px 100px 100px 80px;
  grid-template-columns: repeat(4, 1fr);
  gap: 15px;
  padding: 15px;
  display: grid;
}

