section {
  width: 100%;
  height: 600px;
  background-color: #f5f5f5;
  grid-area: s;
  grid-template-rows: repeat(3, 1fr);
  grid-template-columns: repeat(3, 1fr);
  grid-template-areas: "red orange purple"
                       ". yellow green"
                       ". . blue";
  gap: 5px;
  margin: 0 auto;
  padding: 0 10vw;
  display: grid;
}

section div {
  justify-content: center;
