
* {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
}
:root {
    font-size: 62.5%;
}

body {
    width: 100%;
    font-family: Arial, Helvetica, sans-serif;
    display: grid;
    grid-template-columns: 10vw 1fr 10vw;
    grid-template-rows: 100px 1fr 1fr 150px;
    grid-template-areas: 
    "h h h"
    ". m ."
    "s s s"
    "f f f";
}

header {
    width: 100%;
    padding: 0 10vw 0 10vw;
    background-color: rgb(211, 211, 211);
    height: 100px;
    grid-area: h;
}

nav {
    padding-top: 0.7rem;
    font-size: 1.3rem;
    align-items: flex-end;
    justify-content: flex-start;
    margin-left: 2rem;
    display: flex;
    gap: 2rem;
}


main {
    padding: 15px;
    gap: 15px 15px;
    grid-area: m;
    display: grid;
    grid-template-columns: repeat(4, 1fr) ;
    grid-template-rows: 100px 90px 25px 25px 100px 100px 80px;
}

main div:nth-of-type(1) {
    background-image: url(./images/1.jpg);
    background-position: top;
    background-size: cover;
}

main div:nth-of-type(2) {
    background-image: url(./images/2.jpg);
    background-position: top;
    background-size: cover;
    grid-area: 1 / 2 / 3 / 3;
}

main div:nth-of-type(3) {
    background-image: url(./images/3.jpg);
    background-position: top;
    background-size: cover;
    grid-area: 1 / 3 / 5 / 4;
}

main div:nth-of-type(4) {
    background-image: url(./images/4.jpg);
    background-position: top;
    background-size: cover;
    grid-area: 1 / 4 / 4 / 5;
}

main div:nth-of-type(5) {
    background-image: url(./images/5.jpg);
    background-position: top;
    background-size: cover;
    grid-area: 2 / 1 / 6 / 2;
}

main div:nth-of-type(6) {
    background-image: url(./images/6.jpg);
    background-position: top;
    background-size: cover;
    grid-area: 3 / 2 / 7 / 3;
}

main div:nth-of-type(7) {
    background-image: url(./images/7.jpg);
    background-position: top;
    background-size: cover;
    grid-area: 5 / 3 / 8 / 4;
}

main div:nth-of-type(8) {
    background-image: url(./images/8.jpg);
    background-position: top;
    background-size: cover;
    grid-area: 4 / 4 / 8 / 5;
}

main div:nth-of-type(9) {
    background-image: url(./images/9.jpg);
    background-position: top;
    background-size: cover;
    grid-area: 6 / 1 / 8 / 2;
}

main div:nth-of-type(10) {
    background-image: url(./images/10.jpg);
    background-position: top;
    background-size: cover;
    grid-area: 7 / 2 / 8 / 3;
}

section {
    padding: 0 10vw 0 10vw;
    background-color: #f5f5f5;
    margin: 0 auto;
    width: 100%;
    grid-area: s;
    display: grid;
    height: 600px;
    gap: 5px;

    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(3, 1fr);
    grid-template-areas: 
    "red orange purple"
    " .  yellow green"
    ".   .      blue"
    ;
}

section div {
    display: flex;
    align-items: center;
    justify-content: center;
}

section div:nth-of-type(1) {
    background-color: deeppink;
}

section div:nth-of-type(2) {
    background-color: orange;
}

section div:nth-of-type(3) {
    background-color: purple;
}

section div:nth-of-type(5) {
    background-color: yellow;
}

section div:nth-of-type(6) {
    background-color: green;
}

section div:nth-of-type(9) {
    background-color: blue;
}

footer {
    width: 100%;
    padding: 0 10vw 0 10vw;
   justify-content: center;
   align-items: center;
    display: grid;
    grid-template-columns: 300px 300px 300px;
    grid-template-rows: 150px;
    gap: 20px;
    grid-area: f;
    background-color: #505050;
}

footer div {
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 2rem;
    color: white;    
    height: 80%;
    border: 2px solid white;
}
